  font-family: 'Mulish';
  font-style: normal;
  font-weight: 600;
  src: url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.eot'); /* IE9 Compat Modes */
  src: url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.woff') format('woff'), /* Modern Browsers */
       url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/mulish-v12-vietnamese_latin-ext_latin_cyrillic-ext_cyrillic-600.svg#Mulish') format('svg'); /* Legacy iOS */
}

  .vimeography-thumbnail {
    margin: 0;
    width: 100%;
  }

  /* IE11 Support */
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .vimeography-thumbnail {
      flex: 0 0 25%;
    }
  }

  .vimeography-link {
    display: block;
    margin: 0 0 20px;
    border-radius: 3px;
  }

  .vimeography-link-active {
  }

  .vimeography-thumbnail-img {
    opacity: 0.9;
    transition: all 250ms ease-in-out;
    border-radius: 3px;
    width: 100%;
    padding: 0;
    box-shadow: #000 0em 0em 0em; /* Fixes weird firefox rendering with opacity changes */
    cursor: pointer;

    &:hover {
      opacity: 1;
    }
  }

  figcaption {
    text-align: left;
    position: relative;
    overflow: hidden;
  }

  .vimeography-title,
  /deep/ .vimeography-download {
    color: #222;
    font-family: "Muli", sans-serif;
    font-size: 0.8rem;
    line-height: 1.4rem;
    overflow: hidden;
    font-weight: 600;
    margin: 0;
    padding: 0;
    display: block;
  }
